<template>
    <div id="view-employee" :load="test">
        <ul class="collection with-header">
            <li class="collection-header">
                <h4>{{name}}</h4>
            </li>
            <li class="collection-item">
                Employee ID# <b>{{employee_id}}</b></li>
            <li class="collection-item">Department: <b>{{dept}}</b></li>
            <li class="collection-item">Position: <b>{{position}}</b></li>
        </ul>
        <router-link to='/' class="btn grey">Back</router-link>
        <button @click="deleteEmployee" class="btn red">Delete</button>
        <div class="fixed-action-btn">
            <router-link v-bind:to="{name: 'edit-employee', params: {employee_id: employee_id}}" class="btn-floating btn-large red">
                <i class="fa fa-edit"></i>
            </router-link>
        </div>
    </div>
</template>

<script>
import db from './firebaseInit'

export default {
    name: 'view-employee',
    data(){
        return{
            employee_id: null,
            name: null,
            dept: null,
            position: null,
        }
    },
    beforeRouteEnter (to, from, next) {
        next()
    },
    // beforeRouteEnter (to, from, next) {
    //     console.log("before")
    //     db.collection('employees')
    //         .where('employee_id', '==', to.params.employee_id).get()
    //             .then(querySnapshot => {
    //                 querySnapshot.forEach(doc => {
    //                     next(vm => {
    //                         vm.employee_id = doc.data().employee_id
    //                         vm.name = doc.data().name
    //                         vm.dept = doc.data().dept
    //                         vm.position = doc.data().position
    //                     })
    //                 })
    //             })
    //     next()

    // },
    watch: {
        '$route' (to, from){
            this.fetchData()
        }
    },

    created() {
        this.fetchData()
    },
    methods: {
        test(){
            console.log("OK MAN-----------")
            this.name = "joe king"
        },
        deleteEmployee(){
            if(confirm("Are you sure?")){
                db.collection('employes').where('employe_id', '==', this.$route.params.employee_id).get()
                .then(querySnapshot => {
                    querySnapshot.forEach(doc => { 
                        doc.ref.delete()
                        this.$router.push('/')
                    })
                })
            }
        },
        fetchData(){
            db.collection('employes').where('employe_id', '==', this.$route.params.employee_id).get()
                .then(querySnapshot => {
                    querySnapshot.forEach(doc => { 
                        this.employee_id = doc.data().employe_id
                        this.name = doc.data().name
                        this.dept = doc.data().dept
                        this.position = doc.data().position
                    })
                })
        }
    }
}
</script>



